<template>
  <div id="side-bar">
    <img src="../../assets/icons/profile1.jpg" class="profile"/>
    <p class="title-text">
      {{name}}
    </p>
    <router-link v-for="menuItem in menuItems" :to="menuItem.routing" class="no-underline">
      <p class="menu-text">
        {{menuItem.name}}
      </p>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'side-bar',

  data() {
    return {
      name: "Yifan XIONG",
      menuItems: [{"name":"About Me","routing":"/profile"}, {"name":"My Blog","routing":"/blog"},{"name":"Portefolio","routing":"/portefolio"},{"name":"Contact Me","routing":"/contact"}]
    }
  }
}
</script>

<style scoped>
.profile {
  margin-top: 3rem;
  width: 200px;
  height: auto;
  border-radius: 8px;
}

#side-bar {
  margin-left: 3rem;
}

.title-text {
  margin-top: 2rem;
  font-family: 'lato', sans-serif;
  font-weight: 900;
  font-size: 300%;
  color: #ffffff;
}


.menu-text {
  padding-left: 0.5rem;
  text-align: left;
  margin-top: -1rem;
  margin-bottom: 2.5rem;
  font-family: 'lato', sans-serif;
  font-weight: 400;
  font-size: 150%;
  color: #ffffff;
}

.menu-text:hover {
  cursor: pointer;
  border-left: 3px solid #fb966e;
}
</style>



